html {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "CormorantInfant-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  color: #195557;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.top-wrapper {
  position: relative;
  background: url("../img/fone/Subtract.png") no-repeat;
  background-size: contain;
}
@media (min-width: 600px) {
  .top-wrapper {
    background: linear-gradient(90deg, rgb(234, 238, 239) 45%, rgb(255, 255, 255) 53%);
  }
}
@media (min-width: 1200px) {
  .top-wrapper {
    background: url("../img/fone/subtract.jpg") no-repeat;
    background-size: 100% 97%;
    overflow: hidden;
  }
  .top-wrapper::after {
    content: "";
    position: absolute;
    width: 1048px;
    height: 1048px;
    top: -50%;
    left: -50%;
    background: radial-gradient(50% 50% at 50% 50%, rgba(180, 197, 194, 0.83) 0%, rgba(224, 225, 225, 0) 100%);
  }
  .top-wrapper::before {
    content: "";
    position: absolute;
    width: 600px;
    height: 600px;
    top: 34%;
    right: -130px;
    background: radial-gradient(50% 50% at 50% 50%, rgba(255, 238, 206, 0.49) 0%, rgba(196, 196, 196, 0) 100%);
  }
}

.__container {
  width: 87.5%;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  outline: none;
  box-sizing: border-box;
  width: 100%;
  border: none;
  background: rgb(112, 144, 139);
  box-shadow: 4px 4px 20px #C2CCCA, -8px -8px 20px #FFFFFF;
  border-radius: 40px;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
  font-family: "CormorantInfant-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 114%;
  font-family: "Raleway", "Arial", sans-serif;
  color: #ffffff;
}
.button:hover {
  background: linear-gradient(90.14deg, #B0D3CE -2.41%, #FFEDCE 99.79%);
}

