.header__toggle-menu {
  position: relative;
  margin: 0 86px 0 0;
  width: 25px;
  height: 14px;
  transition-duration: 1s;
  cursor: pointer;
  border: none;
  padding: 0;
  background: transparent;
  z-index: 20;
}
@media (min-width: 768px) {
  .header__toggle-menu {
    margin: 0 90px 0 0;
  }
}
@media (min-width: 860px) {
  .header__toggle-menu {
    display: none;
  }
}

.header__toggle-menu span {
  height: 2px;
  width: 25px;
  background: #195557;
  position: absolute;
  left: 0;
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}

.header__toggle-menu span::before {
  content: "";
  left: 0;
  position: absolute;
  top: -4px;
  height: 2px;
  width: 25px;
  background: #195557;
  transition-duration: 0.25s;
  transition: transform 0.25s, top 0.25s 0.25s;
}

.header__toggle-menu span::after {
  content: "";
  left: 0;
  position: absolute;
  top: 4px;
  height: 2px;
  width: 25px;
  background: #195557;
  transition-duration: 0.25s;
  transition: transform 0.25s, top 0.25s 0.25s;
}

.header__toggle-menu.open span {
  transition-duration: 0.1s;
  transition-delay: 0.25s;
  background: transparent;
}

.header__toggle-menu.open span::before {
  transition: top 0.25s, transform 0.25s 0.25s;
  top: 0px;
  transform: rotateZ(-45deg);
}

.header__toggle-menu.open span::after {
  transition: top 0.4s, transform 0.25s 0.25s;
  top: 0px;
  transform: rotateZ(45deg);
}

